<script setup>
import { onBeforeMount, ref } from "vue";
import { useAuthStore } from "@/stores/auth";
import Berita from "@/api/Berita";

const data = ref()
const message = ref()
const user = ref()


async function hapus(id) {
    if (confirm('Konfirmasi hapus kategori')) {
        const res = await Berita.destroy(id)
        message.value = res.message
        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth'
        })

        setTimeout(() => {
            location.reload()
        }, 2000);
    }
}

onBeforeMount(async () => {
    const authStore = useAuthStore()
    user.value = authStore.user

    data.value = await Berita.all()
})
</script>

<template>
    <h1>Kabar Terkini</h1>

    <v-divider class="mb-4"></v-divider>

    <v-alert type="success" class="my-3" closable close-label="Close Alert" v-if="message">
        {{ message }}
    </v-alert>

    <v-btn color="indigo" class="my-2" prepend-icon="mdi-plus" @click="$router.push('/add-berita')" v-if="user">
        Tambah Kategori
    </v-btn>

    <v-row>
        <v-col v-for="item in data" :key="item._id" cols="6" sm="4">
            <v-card class="mx-auto" max-width="400" :to="`/find-berita/${item._id}`">
                <v-img class="align-end text-white" height="150" :src="'/' + item.gambar" cover>
                    <v-card-title>{{ item.judul }}</v-card-title>

                    <template v-slot:placeholder>
                        <div class="d-flex align-center justify-center fill-height">
                            <v-progress-circular indeterminate color="grey-lighten-4"></v-progress-circular>
                        </div>
                    </template>
                </v-img>

                <v-card-subtitle class="pt-4">
                    Dibuat: {{ (new Date(item.created)).toLocaleDateString() }}
                </v-card-subtitle>

                <v-card-text>
                    <div>{{ item.deskripsi.substring(0, 60) + ' ...' }}</div>
                </v-card-text>

                <v-divider></v-divider>

                <template v-if="user">
                    <v-divider></v-divider>

                    <v-card-actions>
                        <v-btn color="green" :to="`/edit-berita/${item._id}`">Edit</v-btn>
                        <v-btn color="red" @click="hapus(item._id)">Hapus</v-btn>
                    </v-card-actions>
                </template>
            </v-card>
        </v-col>
    </v-row>
</template>